<template>
  <el-dialog v-model="dialogVisible" size="full" :show-close="false" @close="onClose">
    <div slot="title">
      <div class="el-dialog__headerbtn">
        <router-link :to="{name: 'UserProfile'}"><i class="el-dialog__close el-icon el-icon-close"></i></router-link>
      </div>
    </div>
    <slot>
      <div class="container" style="padding-top:125px;">
        <h3>Reset Password</h3>
        <div class="modal-body">
          <span>We will send your reset password link to your email</span>
          <div class="row user-row">
            <el-input v-validate="'required|email'" v-model="email" placeholder="Enter Your Email" name="email">
            </el-input>
            <div class="a-row">
              <span v-show="errors.has('email')" class="help is-danger">{{ errors.first('email') }}</span>
            </div>
          </div>
        </div>
        <div class="footer" style="margin-top:20px; margin-bottom:10px;">
          <el-button type="primary "@click="resetPassword">Reset Password</el-button>
        </div>
      </div>
    </slot>
  </el-dialog>
</template>
<style>
</style>
<script>
  import { db } from '../../../../firebase/vueFire'
  import fireBase from '../../../../firebase/vueFire'
  import { Message } from 'element-ui'
  import _ from 'lodash'
  export default {
    data () {
      return {
        email: '',
        dialogVisible: true
      }
    },
    methods: {
      onClose () {
        this.file = {}
        this.$router.push({name: 'UserProfile'})
      },
      resetPassword () {
        if (this.$validator.validate('email', this.email)) {
          var user = fireBase.auth()
          user.sendPasswordResetEmail(this.email).then(() =>{
            Message({
              showClose: true,
              message: 'Your password reset link has been sent to your email.',
              type: 'success'
            })
          }, function(error) {
            console.log(error)
            Message({
              showClose: true,
              message: error.message,
              type: 'error'
            })
          })
        } else {
          Message({
            showClose: true,
            message: 'There was an error when trying to reset your password.',
            type: 'error'
          })
        }
      }
    },
    mounted () {
      this.$ga.trackPage(this.$route.path)
    }
  }
</script>



